import { useEffect }  from "react";
import { connect }    from "react-redux";
import { useHistory } from "react-router-dom";

import { bindAll }        from "Helpers";
import { useDebounce }    from "Helpers/Hooks";
import { AppraisalSlice } from "Store/Slices";
import Appraisal          from "./Appraisals";

const AppraisalsContainer = ({
	appraisalActions,
}) => {
	const { push } = useHistory();

	const handleSearch = useDebounce((search) => appraisalActions.setSearchFilter(search), 700);

	const handleSearchMunicipality = useDebounce((search) => appraisalActions.setFilters({ municipality : search }), 700);

	const handleChangeFilter = (filter) => (val) => {

		appraisalActions.setFilters({ [filter] : val });
	};

	const handleAddAppraisal = () => push("add_appraisal");

	useEffect(() => {
	  return () => {
			appraisalActions.clearFilters();
	  };
	}, []);

	return (
		<Appraisal
			delegations={{
				handleSearch,
				handleChangeFilter,
				handleAddAppraisal,
				handleSearchMunicipality,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({  AppraisalActions : AppraisalSlice.actions });

export default connect(null, mapDispatchToProps)(AppraisalsContainer);

