import { useRef }                             from "react";
import { InputGroup, InputGroupText }         from "reactstrap";
import { Search }                             from "react-feather";
import { connect, useSelector, shallowEqual } from "react-redux";

import { useFetchSelectOptions }                                                from "Helpers/Hooks";
import { FlatPickrClenable, HeaderPage, FormAsyncSelectGroup, FormSelectGroup } from "Components/Global";
import Input                                                                    from "Components/Global/Input";
import AppraisalTable                                                           from "Components/Appraisal/ApprasialTable";
import "./Appraisals.scss";


const Appraisals = ({
	date,
	state,
	perits,
	delegations : {
		handleSearch,
		handleChangeFilter,
		handleAddAppraisal,
		handleSearchMunicipality,
	},
}) => {

	const placesOptions = useFetchSelectOptions("places");

	return (
		<div id="Appraisals">
			<HeaderPage
				title="Avalúos"
				btnLabel="Nuevo Avalúo"
				handleOnclick={handleAddAppraisal}
				handleSearch={handleSearch}
			/>
			<div className="filters-container mb-2">
				<FormAsyncSelectGroup
					module="users"
					isMulti
					value={perits}
					name="perit"
					placeholder="Filtrar por perito"
					query={{  type : "perit" }}
					onChange={(e) => {
						handleChangeFilter("perits")(e);
					}}
				/>
				<FlatPickrClenable
					date={date}
					placeholder="Filtrar por fecha"
					handleOnChange={handleChangeFilter("date")}
				 />
				<FormSelectGroup
					isMulti
					value={state}
					placeholder="Filtrar por estado"
					options={placesOptions}
					onChange={(e) => handleChangeFilter("state")(e)}
				/>
				<InptSearchMunicipality handleSearchMunicipality={handleSearchMunicipality} />
			</div>
			<AppraisalTable />
		</div>
	);
};

const mapDispatchToState = ({ _appraisalSlice }) => ({
	date   : _appraisalSlice.filters.date,
	perits : _appraisalSlice.filters.perits,
	state  : _appraisalSlice.filters.state,
});

export default connect(mapDispatchToState)(Appraisals);


const InptSearchMunicipality = ({ handleSearchMunicipality}) => {

	const { municipality } = useSelector(state => state._appraisalSlice.filters, shallowEqual );

	const inptRef = useRef(null);

	if (municipality === "" && inptRef.current !== null) {
		inptRef.current.value = "";
	}

	return (
		<div>
			<InputGroup className="input-group-merge">
				<Input
					ref={inptRef}
					placeholder="Filtrar por municipio"
					onChange={(e) => handleSearchMunicipality(e.target.value)}
				/>
				<InputGroupText>
					<Search size={14} />
				</InputGroupText>
			</InputGroup>
		</div>
	);
};
